* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

  #root {
      overflow-x: hidden;
  }

  a {
      text-decoration: none;
  }

  html {
      scroll-behavior: smooth;
  }